<template>
  <div id="app">
      <Navigation></Navigation>
      <div id="scene"></div>
      <router-view/>
  </div>
</template>

<script>
import * as Three from 'three';
import Navigation from './components/Navigation.vue';
export default {
  components: {
    Navigation
  },

  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null
    }
  },
  methods: {

      addStar: function()
        {
          const geoStar = new Three.SphereGeometry(1, 20, 20);
          let materialStar = new Three.MeshBasicMaterial({color: 0x112240, wireframe: false});
          let meshStar = new Three.Mesh(geoStar, materialStar);

          const [x,y,z] = Array(3).fill().map(() => Three.MathUtils.randFloatSpread(200));

          meshStar.position.set(x,y,z);
          this.scene.add(meshStar);
        },
    init: function() {
        let container = document.getElementById('scene');

        this.camera = new Three.PerspectiveCamera(70, container.clientWidth/container.clientHeight, 0.1, 1000);

        // this.camera.position.z = 10;
        // this.camera.position.y = 5;
        // this.camera.rotation.x = -45 * Math.PI / 180;

        

        this.scene = new Three.Scene();

        //set scene to transparent background
        this.scene.background = new Three.Color(0x0A192F);

        // const pointLight = new Three.PointLight(0xffffff, 1);
        // pointLight.position.set(-2, 2, 0);



        //helpers
        // const gridHelper = new Three.GridHelper(200,50);
        // this.scene.add(gridHelper);



 
        for (let i = 0; i < 300; i++) {
          this.addStar();
        }


        this.renderer = new Three.WebGLRenderer({antialias: false});
        this.renderer.setSize(container.clientWidth, container.clientHeight);

        container.appendChild(this.renderer.domElement);
    },

    animate: function() {
        requestAnimationFrame(this.animate);

        //animation here
        this.camera.rotation.y += 0.001;

        this.renderer.render(this.scene, this.camera);
    }
  },
  mounted() {
      this.init();
      this.animate();
  }


}
</script>

<style>

@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,700&display=swap');

#app {
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @apply text-gray-300;

}
body {
  /* background-color: #0D1117; */
  @apply pb-12 bg-background;
  /* background-image: url(./assets/background.png);
  background-repeat: no-repeat; */
}

#scene {
  top:0%;
  left: 0%;
  position: fixed;
  @apply w-full h-full;
  z-index: -1;
}


</style>
